<template>
    <form @submit.prevent="signUp()">
        <h5 class="text-center p-4"> JIPANGE SASA HIVI</h5>
        <div class="form-group">
            <label for="registerPhone">Phone Number</label>
            <input type="text" class="form-control rounded-0"
                   v-model="authData.phone"
                   required
                   id="registerPhone" aria-describedby="phoneHelp" placeholder="Enter Phone Number">
            <small id="phoneHelp" class="form-text text-muted" style="color: red !important;">
                <!--{{ responseErrors && responseErrors.phone ? responseErrors.phone[0] : ''}}-->
            </small>
        </div>

        <!-- Gender -->
        <!-- <div class="form-check form-check-inline">
            <input class="form-check-input" type="radio" name="gender" id="male" value="male" v-model="authData.gender">
            <label class="form-check-label" for="male"> Male </label>
        </div>
        <div class="form-check form-check-inline">
            <input class="form-check-input" type="radio" name="gender" id="female" value="female" v-model="authData.gender">
            <label class="form-check-label" for="female"> Female </label>
        </div> -->

        <!-- County -->
        <!-- <div class="form-group">

            <label for="county"> Select County </label>
            <select class="form-control" id="county" v-model="authData.county_id">
                <option v-for="county in counties" :key="county.id" v-bind:value="county.id"> {{ county.name }}</option>
            </select>
        </div> -->

        <div class="form-group">
            <label for="registerPassword">Password</label>
            <input type="password" class="form-control"
                   v-model="authData.password"
                   required
                   id="registerPassword" placeholder="Password">
            <small id="passwordHelp" class="form-text text-muted" style="color: red !important;" v-if="checkPasswords()">
               Passwords do no match, please check and try again
            </small>
        </div>
        <div class="form-group">
            <label for="confirm_password">Confirm Password</label>
            <input type="password" class="form-control"
                   v-model="authData.password_confirmation"
                   required
                   id="confirm_password" placeholder="Renter Password">
        </div>
        <div class="form-group form-check">

            <input type="checkbox" class="form-check-input" id="hasPromotionCode" v-model="authData.hasPromotionCode">
            <label class="form-check-label" for="hasPromotionCode">
                Do you have the promotion code
            </label>
        </div>
        <div class="form-group" v-if="authData.hasPromotionCode">
            <label for="promotion-code">Enter Promotion code</label>
            <input type="text" class="form-control"
                   v-model="authData.promotionCode"
                   id="promotion-code" placeholder="Enter promotion code">
        </div>
        <div class="form-group form-check">
            <input type="checkbox" class="form-check-input" id="terms" v-model="authData.terms" required>
            <label class="form-check-label" for="terms">
                By clicking you agree to accept our T&C, that you are over 18 and you are aware of our Responsible Gaming Policy
            </label>
        </div>

        <button type="submit"
                v-if="!isLoading"
                :disabled="!authData.phone || !authData.password || !authData.terms"
                class="btn btn-primary rounded-0">Register</button>
        <LoadingIndicator v-if="isLoading"></LoadingIndicator>
        <button @click.prevent="$router.push({name: 'verify-user'})" class="btn btn-primary rounded-0 float-right">I have a verification code</button>

    </form>
</template>

<script>
    import { mapGetters, mapActions } from 'vuex'
    import axios from 'axios'
    import {REGISTER_URL} from '../../api_routes'
    import LoadingIndicator from "../Utilities/LoadingIndicator";
    export default {
        name: "RegisterForm",
        components: {LoadingIndicator},
        data () {
            return {
                authData: {
                    phone: '',
                    password: '',
                    password_confirmation: '',
                    terms: '',
                    promotionCode: '',
                    hasPromotionCode: false
                },
                errors: [],
                isLoading: false
            }
        },

        mounted () {
            this.getAllCounties()
        },
        computed: {
            ...mapGetters(['counties'])
        },
        methods: {
            ...mapActions(['getAllCounties']),
            checkPasswords () {
                if (this.authData.password && this.authData.password_confirmation)
                    return this.authData.password != this.authData.password_confirmation
            },
            signUp () {
                if (this.authData.phone.length < 10) {
                    this.$noty.error('Your phone number is invalid, Please check and try again')
                    return
                }
                this.isLoading = true;
                axios.post(REGISTER_URL, this.authData).then((response) => {
                    let apiResponse = response.data

                    if (response.status === 200) {
                        // Set the user data in local storage
                        let userRegistrationData = {
                            phone: this.authData.phone,
                            password: this.authData.password,
                            regData: apiResponse.data
                        }

                        window.localStorage.setItem('regData', JSON.stringify(userRegistrationData))
                        window.localStorage.setItem('otp_code', JSON.stringify(apiResponse.uuid))
                        this.isLoading = false
                        this.$noty.success('User information has been saved successfully. Please verify your Account')
                        this.$router.push({name: 'verify-user'})
                    }
                }, () => {
                    this.isLoading = false
                    this.$noty.error('The phone number has already been taken, please try another')
                })
            },
        }
    }
</script>

